.portfolio-container {
    width: 95%;
    margin: auto;
    text-align: justify;
    
      border-radius: 10px;
      box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
      padding: 30px;
      margin-top: 30px;
     
    
    
  }
  
  .portfolio-intro {
    padding-top: 50px;
  }
  
  .portfolio-intro h1 {
    font-size: 3rem;
    font-weight: bold;
  }
  
  .portfolio-intro h2 {
    font-size: 2rem;
    font-weight: 300;
    margin-top: 30px;
  }
  
  .portfolio-intro p {
    font-size: 1.3rem;
    font-weight: 300;
    line-height: 1.5;
    margin-top: 40px;
  }
  
  .portfolio-projects {
    margin-top: 70px;
  }
  
  .portfolio-projects h3 {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 40px;
  }
  
  .project {
    width: 90%;
    margin: auto;
    margin-bottom: 40px;
    border: 2px solid #bfbfbf;
    border-radius: 10px;
    padding: 20px;
    position: relative;
  }
  
  .project h4 {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .project p {
    font-size: 1.2rem;
    font-weight: 300;
    line-height: 1.5;
    margin-bottom: 20px;
  }
  
  .project a {
    font-size: 1.2rem;
    font-weight: 400;
    color: #007bff;
    text-decoration: none;
    transition: all 0.3s ease;
    position: absolute;
    bottom: 20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  
  .project a:hover {
    color: #004999;
    text-decoration: underline;
  }
  
  .portfolio-contact {
    margin-top: 70px;
  }
  
  .portfolio-contact h3 {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 40px;
  }
  
  .contact-info {
    margin-bottom: 20px;
    font-size: 1.5rem;
    font-weight: 300;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
  }
  
  .contact-info:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  
  .contact-info svg {
    width: 30px;
    height: 30px;
    margin-right: 10px;
    fill: #bfbfbf;
  }
  
  .contact-info a {
    color: #333333;
    text-decoration: none;
    transition: all 0.3s ease;
  }
  
  .contact-info a:hover {
    color: #007bff;
    text-decoration: underline;
  }
  
  @media only screen and (min-width: 576px) {
    .portfolio-intro h1 {
      font-size: 4rem;
    }
  
    .portfolio-intro h2 {
      font-size: 2.5rem;
    }
  
    .portfolio-intro p {
      font-size: 1.5rem;
      margin-top: 60px;
    }
  
    .portfolio-projects {
      margin-top: 100px;
    }
  
    .project {
      width: 80%;
    }
  
    .project a {
      font-size: 1.5rem;
      bottom: 30px;
    }
  }
  
  .portfolio-contact {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 20px;
  }
